@keyframes up-Down {
  0% {
    transform: translateY(0px);
  }
  100% {
    transform: translateY(-7px);
  }
}

@keyframes r10 {
  0% {
    right: 0px;
  }
  100% {
    right: 10px;
  }
}

@keyframes r20 {
  0% {
    right: 20px;
  }
  100% {
    right: 0;
  }
}

@keyframes r80 {
  0% {
    right: 80px;
  }
  100% {
    right: 0;
  }
}

@keyframes fade {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes visiability {
  100% {
    visibility: visible;
  }
}

@keyframes squeeze {
  0% {
    transform: scale3d(1, 1, 1);
  }
  30% {
    transform: scale3d(1.25, 0.75, 1);
  }
  40% {
    transform: scale3d(0.75, 1.25, 1);
  }
  50% {
    transform: scale3d(1.15, 0.85, 1);
  }
  65% {
    transform: scale3d(0.95, 1.05, 1);
  }
  75% {
    transform: scale3d(1.05, 0.95, 1);
  }
  100% {
    transform: scale3d(1, 1, 1);
  }
}

// shadowing text
@keyframes shadow-text {
  0% {
    filter: blur(0px);
  }
  100% {
    filter: blur(5px);
  }
}

// padding
@keyframes paddddding {
  0% {
    padding: {
      right: 2rem;
      left: 2rem;
    }
  }
  100% {
    padding: {
      right: 5rem;
      left: 5rem;
    }
  }
}

@keyframes smallPad {
  0% {
    padding: {
      right: 1rem;
      left: 1rem;
    }
  }
  100% {
    padding: {
      right: 2.5rem;
      left: 2.5rem;
    }
  }
}
