@import "../helpers/tokens";
@import "../helpers/mixins";

@import "../helpers/fonts";

@import "../global/framework";
@import "../global/reset";
@import "../global/globals";
@import "../global/container";
@import "../global/animation";

@import "../components/header";
@import "../components/footer";
@import "../components/buttons";
@import "../components/sweeper";
@import "../components/contact";
@import "../components/contextMenu";

dialog {
  position: fixed;
  z-index: 1;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 60%;
  aspect-ratio: 1;
  overflow: auto;
  padding-top: 1.5rem;
  transition: opacity 350ms ease-out;
  border: 3px solid var(--alt-clr);
  box-shadow: 0 0 15px var(--alt-clr),
    inset 0 0 10px var(--alt-clr);

  @include breakpoints(max-medium) {
    width: 95%;
  }

  @include breakpoints(mobile) {
    height: 95% !important;
  }
  &::backdrop {
    background-color: rgba(0, 0, 0, 0.65);
  }

  iframe {
    margin: auto;
    display: block;
    width: 80%;
    height: 100% !important;

    @include breakpoints(mobile) {
      width: 100%;
    }
  }

  button {
    position: absolute;
    right: 12px;
    top: 10px;
    border: none;
    color: white;
    font-size: 2.5em;
    background-color: var(--alt-clr);
    transition: transform 300ms ease-out,
      box-shadow 300ms ease-out;
    border-radius: 50%;
    padding-bottom: 0.4rem;
    height: 40px;
    aspect-ratio: 1;
    line-height: 0;

    @include breakpoints(min-small) {
      &:hover {
        transform: rotate(-10deg) scale(0.95);
        box-shadow: 0 0 7px var(--lighty-clr);
      }
    }

    &:focus-visible {
      outline: none;
    }
  }
}
