.contact {
  position: relative;
  padding: 2rem 0;
  background: {
    image: url(../assets/images/all_pages/msgBg.webp);
    repeat: no-repeat;
    size: contain;
    position: 10px;
  }

  @include breakpoints(min-small) {
    padding: 12rem 0;
    background: {
      image: url(../assets/images/all_pages/msgBg.webp);
      repeat: no-repeat;
      size: auto;
      position: 30% center;
    }
  }

  .txt {
    display: flex;
    flex-direction: column;
    gap: 2rem;
    align-items: center;
    justify-content: center;
    width: fit-content;
    margin: auto;
    position: relative;

    &::before {
      content: "";
      position: absolute;
      aspect-ratio: 1/1;
      width: 30px;
      background: white;
      right: 32%;
      bottom: 25%;
      transform: rotate(45deg) translate(-52%, -50%);

      @include breakpoints(min-small) {
        bottom: 16%;
        right: 15%;
      }
      @include breakpoints(max-medium) {
        right: 12%;
      }
      @include breakpoints(min-xlarge) {
        bottom: 17%;
      }

      border: 3px solid;
      border-left-color: transparent;
      border-top-color: transparent;
    }

    input {
      border: 4px solid black;
      border-radius: 20px;

      @include breakpoints(mobile) {
        padding: 1rem 0.5rem;
        top: 3rem;
      }

      @include breakpoints(min-small) {
        font-size: 1.3rem;
        padding: 3rem 2rem;
      }

      @include breakpoints(min-xlarge) {
        font-size: 1.5rem;
        padding: 3rem 2rem;
      }
    }

    button {
      align-self: end;
      transform: translateX(2rem);

      @include breakpoints(mobile) {
        top: 3rem;
      }

      @include breakpoints(min-small) {
        font-size: 1.3rem;
        top: 4rem;
        right: 1rem;
      }

      @include breakpoints(min-xlarge) {
        font-size: 1.5rem;
        top: 10rem;
        right: unset;
      }
    }
  }
}
