// framework

.img-res {
  max-width: 100%;
  height: auto;
}

.capitalize {
  text-transform: capitalize;
}

.upper-case {
  text-transform: uppercase;
}

.center {
  text-align: center;
}

.f-center {
  display: flex;
  justify-content: center;
  align-items: center;
}

.g-center {
  display: grid;
  & > * {
    place-self: center;
  }
}
.bold {
  font-weight: bold;
}

.withBg {
  &::before {
    content: "";
    background-image: url(../assets/images/contact/background-01.webp);
    background-size: contain;
    background-repeat: no-repeat;
    position: absolute;
    aspect-ratio: 1;
    width: 100px;
    z-index: -1;
    transform: translate(-22%, -35%) rotate(260deg);
  }
}

body.arabic {
  .withBg {
    direction: rtl;
    &::before {
      transform: translate(15%, -25%) rotate(-260deg) !important;
    }
  }
}

.figHover {
  perspective: 100px;

  img {
    transition: transform 250ms ease-out;
  }
  &:hover {
    img {
      transform: translate3d(-15px, -10px, -5px);
    }
  }
}
