header {
  padding-top: 1.2rem;

  .container {
    display: flex;
    justify-content: space-between;
    align-items: center;

    @include breakpoints(mobile) {
      flex-direction: column;
      gap: 0.5rem;
    }

    img {
      height: 80px;
      cursor: pointer;
    }
    div {
      user-select: none;

      & > a {
        position: relative;
      }
      & > a::before {
        content: "";
        position: absolute;
        width: 0%;
        height: 2px;
        background-color: var(--theme-clr);
        bottom: 0;
        left: 0;
        transition: width 0.3s ease-in-out;
      }
      & > a:hover::before {
        width: 100%;
      }

      & > *:hover:not(p) {
        color: var(--theme-clr);
        cursor: pointer;
      }
      i {
        margin-inline-end: 15px;
        font-size: 1.3em;
        cursor: pointer;
      }
      p {
        display: inline;
        padding: {
          left: 5px;
          right: 5px;
        }
      }
    }
  }
}
