.channelsBanner {
  padding: 6rem 1rem;

  @include breakpoints(max-medium) {
    background-size: cover;
  }

  .bgGlobal {
    position: absolute;
    z-index: -99;

    &:nth-child(1) {
      max-width: calc(clamp(350px, 70vw, 1200px));
      top: 0;
      left: 0;

      &.ar {
        left: unset;
        right: 0;
        transform: scaleX(-1);
      }
    }

    &:nth-child(2) {
      max-width: 10vw;
      right: 8rem;
      top: 17rem;
      transform: rotate(45deg);

      &.ar {
        left: 8rem;
        right: unset;
      }

      @include breakpoints(mobile) {
        display: none;
      }
    }
  }

  .container {
    .channelsContent {
      display: flex;
      align-items: center;
      gap: 2rem;

      &.ar {
        @include breakpoints(min-small) {
          flex-direction: row-reverse;
        }
      }

      @include breakpoints(mobile) {
        flex-direction: column;
      }
    }
    img {
      aspect-ratio: 1/1;
      width: 80px;
    }

    h1 {
      font: {
        size: 3.5rem;
        family: harrington, sans-serif;
      }

      @include breakpoints(mobile) {
        text-align: center;
      }
    }
  }
}
