// Globals
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

:root {
  --theme-clr: #ff6f8b;
  --alt-clr: #f4a1a4;
  --lighty-clr: #ff6f8b99;
  --txt-clr: #250000;
  --bg-clr: #ffdbdb;
}

::-webkit-scrollbar {
  width: 8px;
}
::-webkit-scrollbar-track {
  background: var(--bg-clr-alt);
}
::-webkit-scrollbar-thumb {
  background-color: var(--theme-clr);
  border-radius: 20px;
  border: 2px solid var(--bg-clr-alt);
}

::selection {
  background-color: var(--lighty-clr);
}

html {
  scroll-behavior: smooth;
}

body {
  font: {
    family: mmrtext, sans-serif;
    size: 20px;
  }
  color: var(--txt-clr);
}
body.arabic {
  font-family: GESS, sans-serif;
}

a {
  text-decoration: none;
  color: inherit;
}

button {
  background-color: transparent;
  border: none;
}

input,
textarea {
  caret-color: var(--theme-clr);
  color: var(--txt-clr);

  &:focus {
    outline: none;
  }
}

.invisible {
  display: none !important;
}

.visible {
  display: block !important;
}

.ar {
  text-align: right;
}

h1 {
  @include breakpoints(max-medium) {
    // font-size: 2.3rem !important;
  }
}

h2 {
  font-size: 2rem;
  @include breakpoints(min-xlarge) {
    font-size: 2.5rem;
  }
}

p {
  line-height: 1.5;
  font-size: 1.5rem;
}
.lds-hourglass {
  display: inline-block;
  position: relative;
  width: 80px;
  height: 80px;
}
.lds-hourglass:after {
  content: " ";
  display: block;
  border-radius: 50%;
  width: 0;
  height: 0;
  margin: 8px;
  box-sizing: border-box;
  border: 32px solid var(--alt-clr);
  border-color: var(--alt-clr) transparent var(--alt-clr)
    transparent;
  animation: lds-hourglass 1.2s infinite;
}
@keyframes lds-hourglass {
  0% {
    transform: rotate(0);
    animation-timing-function: cubic-bezier(
      0.55,
      0.055,
      0.675,
      0.19
    );
  }
  50% {
    transform: rotate(900deg);
    animation-timing-function: cubic-bezier(
      0.215,
      0.61,
      0.355,
      1
    );
  }
  100% {
    transform: rotate(1800deg);
  }
}

.sweeper .swiper {
  max-width: 100%;
}
