footer {
  background: {
    image: url("../assets/images/all_pages/footer-01.webp");
    size: cover;
    repeat: no-repeat;
    position: center;
  }
  margin-top: 2rem;
  padding: {
    top: 1rem;
    bottom: 2rem;
  }

  @include breakpoints(mobile) {
    padding-top: 4rem;
  }

  img {
    max-width: 70px;
    padding-top: 2rem;
  }

  .footing1,
  .footing2 {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    gap: 1rem;

    a {
      &:not(:last-child) p::after {
        content: "|";
        margin-inline-start: 1rem;
        color: #fff;
      }

      &:hover p {
        color: white;
        text-shadow: var(--theme-clr);
        filter: drop-shadow(2px 5px 5px var(--theme-clr));
      }
    }
  }

  .footing1 {
    margin: {
      top: 0.8rem;
      bottom: 1.5rem;
    }

    @include breakpoints(mobile) {
      flex-direction: column;
      a p::after {
        content: none !important;
      }
    }
  }

  .footing2 {
    padding-bottom: 1rem;
  }
}
