.contact-form {
  padding: {
    top: 10rem;
    bottom: 10rem;
  }
  @include breakpoints(large) {
    overflow: hidden;
  }
  @include breakpoints(mobile) {
    overflow: hidden;
  }

  position: relative;
  .bg {
    position: absolute;
    max-height: 130%;
    top: 20px;

    @include breakpoints(large) {
      top: -102px;
    }

    @include breakpoints(medium) {
      max-width: 100%;
    }

    @include breakpoints(max-small) {
      max-width: 100%;
    }
    @include breakpoints(mobile) {
      display: none;
    }
  }

  .container {
    .box {
      margin: auto;
      width: 13rem;
      position: relative;
      top: 145px;

      @include breakpoints(large) {
        top: -74px !important;
      }

      @include breakpoints(medium) {
        top: 106px;
      }

      .header {
        position: relative;
        bottom: 18px;
        h2 {
          margin-right: 1rem;
          font-weight: bold;
          font-size: 1.4rem;
        }

        img {
          max-width: 60px;
        }
      }

      .form {
        flex-direction: column;
        position: relative;
        top: -13px;
        margin: auto;

        input,
        textarea {
          width: 100%;
          border: none;
          background-color: transparent;

          &:focus,
          &:hover {
            color: var(--theme-clr);
            text-shadow: 0 0 15px var(--theme-clr);
          }

          @include breakpoints(max-medium) {
            border: var(--theme-clr);
            background-color: var(--lighty-clr);
            border-radius: 0.8rem;
            padding: 0.5rem 1rem !important;
          }
        }

        input {
          &:not(:last-child) {
            margin-bottom: 1rem;
          }

          &.specBtn {
            background-color: white;
            height: initial;
            width: 100px;

            position: relative;
            top: 10px;
          }
          padding-left: 0.5rem;
        }

        textarea {
          padding: {
            left: 0.5rem;
            top: 0.4rem;
          }
          margin-bottom: 1rem;
          height: 5rem;
          resize: vertical;
        }
      }
    }
  }

  &:not(.alt-form) {
    .container .box .form {
      max-width: 360px;
    }
  }
}

body.arabic {
  .contact-form .container .box {
    input,
    textarea {
      direction: rtl;
    }

    div {
      text-align: right;

      h2 {
        text-align: center;
      }
    }
  }
}
