#ctxmenu {
  position: fixed;
  z-index: 10000;
  width: 150px;
  background-color: white;
  border: 1px solid var(--theme-clr);
  border-radius: 5px;
  display: none;

  & > div {
    display: flex;
    flex-direction: column;
    &:not(:last-child) {
      border-bottom: 2px solid var(--theme-clr);
      padding-bottom: 0.5rem;
    }
    &:last-child {
      padding-top: 0.5rem;
    }
  }

  a.item {
    padding: 5px 10px;
    font-size: 15px;
    color: var(--txt-clr);
    cursor: pointer;
    transition: padding 0.3s ease-out;

    &:hover {
      color: white;
      padding-left: 20px;
      background-color: var(--alt-clr);
    }
  }
}

body.arabic {
  #ctxmenu {
    text-align: right;

    a.item:hover {
      padding-right: 20px;
    }
  }
}
