@mixin object($width, $height, $bg) {
  width: $width;
  height: $height;
  background: $bg;
}

@mixin transPos(
  $top,
  $right,
  $bottom,
  $left,
  $transX,
  $transY
) {
  position: absolute;
  top: $top;
  left: $left;
  right: $right;
  bottom: $bottom;
  transform: translate($transX, $transY);
}

.reacts {
  @include object(max-content, max-content, #fff);
  border-radius: 90px;
  padding: 15px;
  display: flex;
  flex-flow: row wrap;
  justify-content: space-around;
  box-shadow: 0 0 2.5rem #00000033;
  opacity: 0;
  overflow: hidden;
  position: absolute;
  gap: 1.3rem;
  bottom: 40px;
  z-index: 99;
  transition: opacity 0.35s ease-in-out;

  .circle {
    @include object(2rem, 2rem, #edff97);
    border-radius: 50%;
    transform-origin: 50% 100%;
    animation: slide 0.2s ease-in-out forwards;
    cursor: pointer;
    @keyframes slide {
      0% {
        transform: translateY(30px);
        opacity: 0;
      }
    }
    @for $i from 1 through 6 {
      &:nth-child(#{$i}) {
        animation-duration: calc(0.2s * #{$i});
      }
    }
    &:hover {
      transform: scale(1.15);
    }
    &:hover p {
      opacity: 1;
    }
    img {
      width: 100%;
    }
    position: relative;
    p {
      @include transPos(-30px, null, null, 50%, -50%, 0);
      background: rgba(#000, 0.5);
      padding: 4px 8px;
      display: block;
      color: #fff;
      font-size: 10px;
      border-radius: 90px;
      opacity: 0;
    }
  }
}

.show {
  opacity: 1;
}

// button styles
.specBtn {
  font-size: 20px;
  background-color: #ff6f8b;
  box-shadow: 0px 1px 10px grey;
  border-bottom-right-radius: 20px;
  border-top-left-radius: 20px;
  padding: 3px 7px;
  //   animation: up-Down 1s ease-in-out infinite alternate
  //     paused;
  cursor: pointer;
  border: 1px solid transparent;

  &:hover {
    background-color: white;
    color: var(--theme-clr);
    // animation-play-state: running;
    border-color: var(--theme-clr);
    box-shadow: 7px 6px 15px var(--lighty-clr);
  }
}
