// sweeper buttons
.btns {
  display: flex;
  gap: 0.5rem;
  bottom: -4.5rem;
  position: absolute;
  justify-content: center;
}

// bgs
.swp {
  position: relative;
  height: fit-content;

  img.bgs {
    position: absolute;
    top: 70%;
    right: 0;
    width: 300px;
    z-index: -2;

    @include breakpoints(large) {
      top: 85%;
    }

    &:nth-child(2) {
      top: 0;
      right: unset;
      left: 0;
    }

    @include breakpoints(mobile) {
      display: none;
    }
  }

  span.bg-words {
    position: absolute;
    top: 75%;
    right: 55px;
    z-index: -1;

    @include breakpoints(large) {
      top: 88%;
    }

    @include breakpoints(mobile) {
      display: none;
    }

    p {
      position: relative;
      font: {
        size: 3rem;
        family: euroLight, sans-serif;
      }

      &:first-child {
        animation: r10 3s ease-in-out infinite alternate;
      }

      &:nth-child(2) {
        right: 50px;

        animation: r20 3s ease-in-out infinite alternate;
      }
      &:nth-child(3) {
        right: 10px;
        span {
          position: relative;
          display: inline-block;
          &:first-child {
            animation: squeeze 2s 3s ease-in-out infinite
              alternate;
          }
          &:nth-child(2) {
            animation: squeeze 2s 3.2s ease-in-out infinite
              alternate;
          }
          &:nth-child(3) {
            animation: squeeze 2s 3.4s ease-in-out infinite
              alternate;
          }
          &:nth-child(4) {
            animation: squeeze 2s 3.6s ease-in-out infinite
              alternate;
          }
          &:nth-child(5) {
            animation: squeeze 2s 3.8s ease-in-out infinite
              alternate;
          }
          &:nth-child(7) {
            animation: squeeze 2s 4s ease-in-out infinite
              alternate;
          }
        }
      }
      &:last-child {
        right: 80px;
        animation: r80 3s ease-in-out infinite alternate;
      }
    }
  }

  @include breakpoints(min-small) {
    margin-top: 10rem;
    img.bgs,
    .bg-words {
      opacity: 0.3;
    }
  }

  @include breakpoints(min-xlarge) {
    img.bgs,
    .bg-words {
      opacity: 1;
    }
  }
}

.sweeper {
  .swiper {
    padding: {
      top: 4rem;
      bottom: 6rem;
    }

    .swiper-wrapper {
      .swiper-slide {
        background-position: center;
        background-size: cover;
        // [ ] height of the banner here!!
        width: 180px;
        height: 260px;
        display: flex;
        justify-content: center;

        position: relative;

        &.swiper-slide-active {
          img {
            transform: scale(1.4);
          }
        }

        img {
          position: absolute;
          bottom: 0;
          max-width: 150px;
          max-height: 100%;
          transition: transform 0.4s ease-in-out;
        }
      }
    }
  }

  .swiper-3d .swiper-slide-shadow-left,
  .swiper-3d .swiper-slide-shadow-right {
    background-image: none;
  }

  .btns {
    opacity: 0;
    transition: opacity 0.5s ease-in-out;
  }
}

.swiper-slide-active {
  .btns {
    opacity: 1;
  }
}
