.container {
  width: 100%;
  margin: {
    left: auto;
    right: auto;
  }
  padding: {
    left: 3rem;
    right: 3rem;
  }

  @include breakpoints(min-large) {
    padding: {
      left: 8rem;
      right: 8rem;
    }
  }

  @media (min-width: 1800px) {
    padding-left: 10vw;
    padding-right: 10vw;
  }
  @include breakpoints(mobile) {
    padding: {
      left: 0.9rem;
      right: 0.9rem;
    }
  }

  //   responsive padding
  //   padding: $top-pad #{"min(3rem,5vw)"};

  @supports (-ms-ime-align: auto) {
    // padding: 10rem 5rem;
  }

  position: relative;

  @include breakpoints(max-small) {
    max-width: 540px;
    // For Edge
    @supports (display: -ms-grid) {
      //   padding: 8rem 2rem;
    }
  }
  @include breakpoints(medium) {
    max-width: 720px;
    // For Edge
    @supports (-ms-ime-align: auto) {
      //   padding: 10rem 3rem;
    }
  }
  @include breakpoints(large) {
    max-width: 960px;
    // For Edge
    @supports (-ms-ime-align: auto) {
      //   padding: 10rem 4rem;
    }
  }
}

$container-max-widths: (
  sm: 540px,
  md: 720px,
  lg: 960px,
  xl: 1140px,
  xxl: 1320px,
);
