@import "pages/main";

@import "components/channelsBanner";

@import "components/contactForm";

.about-page {
  .swp {
    margin: {
      bottom: 5rem;
    }

    img.bgs {
      top: unset;
    }

    .bg-words {
      top: 20px;
    }
  }

  section.founder {
    margin: {
      top: 3rem;
      bottom: 2rem;
    }

    @include breakpoints(min-xlarge) {
      margin-bottom: 5rem;
    }

    .f-boxes {
      display: flex;
      flex-wrap: wrap;
      justify-content: center;
      width: fit-content;
      margin: auto;
      gap: 5rem;

      .box {
        display: grid;
        & > * {
          justify-self: center;
          justify-items: center;
          align-self: end;
        }

        img {
          max-width: 200px;
        }
        p {
          font-size: 2rem;
        }
      }
    }
  }

  .contact-form {
    @include breakpoints(large) {
      overflow: visible !important;
      margin-top: 10rem;

      .box {
        top: 27px !important;
      }
    }

    @include breakpoints(medium) {
      .box .form {
        top: -25px;

        input:not(:last-child) {
          margin-bottom: unset;
        }

        input,
        textarea {
          background-color: transparent;
        }
      }
    }

    @include breakpoints(mobile) {
      padding-top: unset;
    }
  }
}
